import React from "react";
import { useHistory } from "react-router";
import { PASSWORD } from "../constants/.admin";

export default function useCertification() {
  const history = useHistory();
  React.useEffect(() => {
    const password = prompt(
      "    "
    );

    if (password !== PASSWORD) {
      alert(" ");
      history.push("/");
    }
  }, [history]);
}
